import { LoadingOutlined, ScissorOutlined, FormOutlined, SmileOutlined, SolutionOutlined, CheckCircleOutlined, UserOutlined, SendOutlined, CopyOutlined} from '@ant-design/icons';
import { App, Button, Col, DatePicker, Divider, Drawer,  Image,  Row,  Space, Steps, Table, Tag } from "antd";
import { useState } from "react";
import useAPIKey from "../../hooks/useAPIKey";




 export default function Home (){

    // API Hooke
    const [api, setAPI] = useAPIKey();

    // Drawer
    const [openDrawer, setOpenDrawer] = useState(false);

    // message moal
    const {message, modal} = App.useApp();
    

    // status Step
    const [currentStep, setCurrentStep] = useState(0);
    //
    const showMsg = ()=>{

        modal.info({title:'useAPP title', content:'useApp content'});        
    }

    //
    const columns = [
        {
          title: '物料',
          dataIndex: 'name',
          ellipsis: true,
          width: 80
        },
        {
          title: '供应商',
          dataIndex: 'provider',
          ellipsis: true
        },
        {
          title: '规格',
          dataIndex: 'spc',
          ellipsis: true,
          width: 60
        },
        {
          title: '数量',
          dataIndex: 'count',
          ellipsis: true,
          width: 45
        },
        {
          title: '单价',
          dataIndex: 'price',
          ellipsis: true,
          width: 45
        },
        {
          title: '操作',
          ellipsis: true,
          width: 45,
          render: (item)=><Space>
            <a> 编辑 </a>
          </Space>
        },
      ];
      const data = [
        {
          key: '1',
          name: '花边布料',
          provider: '中大市场 xx xx xx',
          spc: 32,
          count: 2,
          price: 300
        },
        {
          key: '2',
          name: '纽扣',
          provider: '中大市场 xx xx xx',
          spc: 32,
          count: 2,
          price: 300
        },
        {
          key: '3',
          name: '拉链',
          provider: '本地工厂 xx xx xx',
          spc: 32,
          count: 2,
          price: 300
        },
      ];
    return (
        <>
                <Row 
                    // justify='space-between' 
                    align='middle'>

                    <Col span={14}>
                        <Image 
                            
                            width={'200px'}
                            height={'300px'}
                            src="error"
                            fallback="data:image/png;base64,"
                        />
                    </Col>
                    <Col span={10}>
                        <Steps
                            
                            current={currentStep}
                            direction='vertical'
                            // responsive={false}
                            items={[
                            {
                                title: '订单需求',
                                // subTitle:'subtitle',
                                description: '2023.05.05',
                                icon: <FormOutlined />
                            },
                            {
                                title: '打板 / 样板',
                                description: 'xxx 2023.05.07',
                                icon: <ScissorOutlined />,
                            },
                            {
                                title: '用户确认',
                                description: 'xxx 2023.05.07',
                                icon: <> <SolutionOutlined /> </>,
                            },
                            {
                                title: '投产生产',
                                description: 'xxx 2023.05.07',
                                icon: <CopyOutlined />,
                            },
                            {
                                title: '交付',
                                description: 'xxx 2023.05.07',
                                icon: <SendOutlined />,
                            },
                            {
                                title: 'Done',
                                description: 'xxx 2023.05.07',
                                icon: <CheckCircleOutlined />,
                            },
                            ]}
                        />
                    </Col>
                </Row>

            


            {/* <Divider orientation='left'>物料 & 清单</Divider> */}
            <Table columns={columns} dataSource={data} scroll={{y:100}} pagination={{position:['none','none']}}/>

            <Divider/>
            <Table columns={columns} dataSource={data} scroll={{y:100}} pagination={{position:['none','none']}}/>

            <Divider/>
            <Space.Compact block>
                {currentStep>0 && <Button block onClick={()=>setCurrentStep(currentStep-1)}>上一步</Button>}
                {
                    currentStep < 5 ?
                    <Button block type='primary' onClick={()=>setCurrentStep(currentStep+1)}>下一步</Button>
                    :
                    <Button block type='primary' onClick={()=>message.info('Done')}>完成</Button>
                
                }
            </Space.Compact>
            <Drawer
                title='title'
                // size="large"
                placement="bottom"
                // maskClosable={false}
                open={openDrawer}
                closable={false}
                onClose={()=>setOpenDrawer(false)}
            >


                <Steps 
                    direction="vertical"
                    // type="inline"
                    
                    // type="navigation"
                    // percent={10}
                    current={currentStep}
                    // onChange={(e)=>console.log(e)}
                    labelPlacement='horizontal'
                    items={[
                        {
                            title: "Finished",
                            description: "desc",
                            icon: <FormOutlined />
                            
                        },
                        {
                            title: "In Progress",
                            description: "desc",
                            subTitle: 'Left 00:00:08',
                        },
                        {
                            title: "Finished",
                            description: "desc",
                            
                        },
                    ]}
                />
            </Drawer>


            

         

            
        </>
    )
}


